@use '../../setting' as s;
@use '../../mixin' as m;
.page-photo-gallery_overlay {
	position: fixed;
	top: 0;
	right: 0;
	bottom: 0;
	left: 0;
	@include m.zIndex(modal);
	display: flex;
	background: rgba(0, 0, 0, 0.8);

	&_cross {
		width: 16px;
		height: 16px;
		position: absolute;
		top: 18px;
		right: 12px;
		cursor: pointer;

		@include s.mq() {
			width: 28px;
			height: 28px;
			top: 64px;
			right: 40px;
		}

		span {
			width: 25px;
			height: 2px;
			background: s.$baseColor;
			position: absolute;
			top: 50%;
			left: 50%;

			@include s.mq() {
				width: 40px;
			}

			&:nth-of-type(1) {
				transform: translate(-50%, -50%) rotate(45deg);
			}

			&:nth-of-type(2) {
				transform: translate(-50%, -50%) rotate(-45deg);
			}
		}
	}

	&_img {
		height: 100%;
		object-fit: contain;

		&_wrapper {
			max-width: 95%;
			height: 80%;
			margin: auto;

			@include s.mq() {
				max-width: 60%;
			}
		}
	}
}
