@use '../../setting' as s;
@use '../../mixin' as m;
.page-question_list {
	&_inner {
		background: s.$subColor02;
		border-radius: 40px;
		margin: 32px auto 0;
		width: 92%;
		max-width: s.$contentWidth + 80px * 2;
		overflow: hidden;

		@include s.mq() {
			padding: 80px;
			margin-top: 24px;
		}
	}

	&_contents {
		padding: 40px;
		max-width: s.$contentWidth;

		@include s.mq() {
			padding: 0;
		}
	}
}
