@use '../../setting' as s;
@use '../../mixin' as m;
.top_kv {
	padding-top: 80px;

	@include s.mq(xl) {
		margin-top: 8px;
		padding-top: 0;
	}

	&_panels {
		display: flex;
		position: absolute;
		top: 80px;
		left: 0;

		@include s.mq() {
			top: 120px;
		}

		@include s.mq(lg) {
			top: 0;
		}

		&_wrapper {
			height: calc(100vh - 180px);
			width: 100%;
			padding-top: 80px;
			position: relative;
			overflow: hidden;
			box-sizing: content-box;

			@include s.mq(lg) {
				padding-top: 0;
			}

			@include s.mq() {
				height: 80vh;
			}
		}
	}

	&_panel {
		height: calc(100vh - 180px);
		width: 92vw;
		margin: 0 4px;
		background-size: cover;
		background-position: center;

		@include s.mq() {
			height: 80vh;
		}

		@include s.mq(xl) {
			width: 1200px;
		}

		&:not(:first-child) {
			margin-left: 8px;
		}
	}

	&_contents {
		position: relative;
		padding-top: 160px;
		@include m.zIndex();

		@media screen and (min-width: 340px) {
			padding-top: 240px;
		}

		@include s.mq() {
			padding-top: calc(80vh - 240px);
			display: flex;
			justify-content: space-between;
			align-items: flex-end;
		}
	}

	&_label {
		&_wrapper {
			width: 120px;
			position: absolute;
			top: 24px;
			right: 4%;

			@media screen and (min-width: 340px) {
				width: 140px;

				top: 56px;
			}

			@include s.mq() {
				width: 170px;
				right: 64px;
			}
		}
	}

	&_more-btn {
		&_wrapper {
			display: block;
			width: 200px;
			position: absolute;
			top: -8px;
			left: 0;
			transform: translateY(-100%);

			@include s.mq(lg) {
				width: 320px;
				position: static;
				transform: none;
			}
		}
	}

	&_prev {
		width: 32px;
		position: absolute;
		top: 50%;
		left: 6vw;
		transform: translateY(-50%);
		background: transparent;
		display: none;

		@include s.mq() {
			display: block;
		}

		@include s.mq(lg) {
			left: 120px;
		}
	}

	&_next {
		width: 32px;
		position: absolute;
		top: 50%;
		right: 6vw;
		transform: translateY(-50%) rotate(180deg);
		background: transparent;
		display: none;

		@include s.mq() {
			display: block;
		}

		@include s.mq(lg) {
			right: 120px;
		}
	}
}

.top_kv-copy {
	&_wrapper {
		max-width: 600px;

		@include s.mq() {
			padding-bottom: 40px;
		}
	}
}

.top_kv-nav {
	margin-top: 15vh;
	display: flex;
	justify-content: center;
	position: relative;
	@include m.zIndex();

	@include s.mq() {
		margin-top: 24px;
		justify-content: start;
	}
	&_dot {
		width: 9px;
		height: 9px;
		border-radius: 50%;
		background: #777474;

		&:not(:first-of-type) {
			margin-left: 15px;
		}
	}
}

.top_kv-scroll-down {
	display: flex;
	flex-direction: column;
	align-items: center;
	margin-top: -24px;
	position: relative;
	@include m.zIndex();

	&_txt {
		letter-spacing: 0.1em;
	}

	&_dot {
		width: 10px;
		object-fit: contain;
		object-position: center;
	}

	&_bar {
		margin: 0 auto;
		width: 2px;
		height: 32px;
		margin-top: 0;
		position: relative;
		border-radius: 1px;
		overflow: hidden;

		&::after {
			content: '';
			width: 2px;
			height: 80px;
			background: #000;
			border-radius: 1px;
			position: absolute;
			top: 0;
			left: 0;
			transform: translateY(-100%);
			animation: scrollDown 1.6s ease-in-out infinite;

			@keyframes scrollDown {
				0% {
					transform: translateY(-100%);
				}

				96% {
					transform: translateY(100%);
				}

				100% {
					transform: translateY(100%);
				}
			}
		}
	}
}
