@use '../../setting' as s;
@use '../../mixin' as m;
.ly_footer {
	padding: 56px 0 24px;
	background-size: cover;
	background-position: center;
	position: relative;

	@include s.mq(lg) {
		padding: 72px 0;
	}

	&::before {
		content: '';
		position: absolute;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		background: rgba($color: #fff, $alpha: 0.7);
	}

	&_contents {
		position: relative;
		@include m.zIndex();
	}

	&_logo {
		max-width: 360px;
		width: 80%;
		margin: 0 auto;

		&_wrapper {
			width: 100%;
			position: relative;

			&::before {
				content: '';
				height: 1px;
				width: calc(50% - 80% / 2 - 8px);
				background: #000;
				position: absolute;
				left: 0;
				top: 50%;

				@include s.mq() {
					width: calc(50% - 360px / 2 - 16px);
				}
			}

			&::after {
				content: '';
				height: 1px;
				width: calc(50% - 80% / 2 - 8px);
				background: #000;
				position: absolute;
				right: 0;
				top: 50%;

				@include s.mq() {
					width: calc(50% - 360px / 2 - 16px);
				}
			}
		}
	}

	&_border {
		height: 1px;
		width: 100%;
		background: #000;
		display: block;
	}

	&_bottom {
		width: 90%;
		margin: 56px auto 0;

		@include s.mq() {
			width: 100%;
			margin-top: 24px;
			display: flex;
			justify-content: space-between;
			align-items: center;
		}
	}

	&_main-address {
		font-size: 14px;
		margin-top: 56px;
	}
}

.ly_footer-nav {
	padding: 56px 32px;

	@include s.mq() {
		padding: 72px 0;
	}

	&_items {
		margin-top: -24px;

		@include s.mq(lg) {
			width: 780px;
			display: flex;
			flex-wrap: wrap;
		}
	}

	&_item {
		@include s.mq(lg) {
			margin-top: 24px;
		}

		&:not(:first-of-type) {
			margin-top: 8px;
		}

		&:not(:first-of-type) {
			@include s.mq(lg) {
				margin-left: 64px;
			}
		}

		&:nth-of-type(6) {
			@include s.mq(lg) {
				margin-left: 0;
			}
		}
	}
}

.ly_footer-sns {
	display: flex;

	&_item {
		padding-left: 24px;
		position: relative;

		&:not(:first-of-type) {
			margin-left: 32px;
		}

		&__facebook::before {
			content: '';
			width: 18px;
			height: 18px;
			background: url(s.$imgDir + 'logo-facebook.png') center / contain
				no-repeat;
			position: absolute;
			top: 50%;
			left: 0;
			transform: translateY(-50%);
		}

		&__instagram::before {
			content: '';
			width: 18px;
			height: 18px;
			background: url(s.$imgDir + 'logo-instagram.png') center / contain
				no-repeat;
			position: absolute;
			top: 50%;
			left: 0;
			transform: translateY(-50%);
		}
	}

	&_link {
		letter-spacing: 0.1em;
	}
}
