@use '../../setting' as s;
@use '../../mixin' as m;
.bl_page-header {
	padding-top: 120px;
	position: relative;

	&_img {
		& > img,
		source {
			height: 100%;
			object-fit: cover;
			object-position: 40% center;
		}

		&_wrapper {
			height: 72vh;
			margin-top: 56px;
			position: relative;

			@include s.mq() {
				height: auto;
				margin-top: 120px;
			}
		}
	}

	&_title-sub {
		color: #e5e3e3;
		font-size: 40px;
		letter-spacing: 0.1em;
		position: absolute;
		top: 0;
		transform: translateX(50%) rotate(90deg);
		right: 12%;

		@include s.mq() {
			font-size: 64px;
		}
	}

	&_menu {
		position: absolute;
		bottom: 8px;
		left: 8px;

		@include s.mq(xl) {
			display: none;
		}

		&_item {
			height: 64px;

			&:not(:first-child) {
				margin-top: 10px;
			}
		}

		&_link {
			display: block;
			height: 100%;
		}

		&_img {
			object-fit: contain;
			height: 100%;
			width: auto;
		}
	}
}
