@use '../setting' as s;
.ly_section {
	padding: 40px 0;
	overflow: hidden;

	@include s.mq() {
		padding: 72px 0;
	}
}

.ly_section-pop {
	background: url(s.$imgDir + 'bg_section_sm.png') center top / cover;
	padding-bottom: 72px;
	overflow: hidden;

	@include s.mq(lg) {
		background: url(s.$imgDir + 'bg_section_lg.png') center top / cover;
	}
}
