@use '../../setting' as s;
@use '../../mixin' as m;
.page-about_coaching {
	padding-bottom: 80px;

	&_title {
		font-size: 16px;
		font-weight: bold;
		letter-spacing: 0.1em;
		line-height: 1.2;
		border-left: 5px solid s.$accentColor02;
		padding-left: 8px;

		@include s.mq() {
			font-size: 18px;
		}
	}

	&_imgs {
		margin-top: 40px;
		width: 100%;
		overflow: hidden;
		display: flex;
	}

	&_img {
		&_wrapper {
			width: calc(100% / 3);

			&:nth-child(n + 4) {
				display: none;

				@include s.mq() {
					display: block;
				}
			}

			@include s.mq() {
				width: calc(100% / 5);
			}
		}
	}

	&_btn {
		&_wrapper {
			margin-top: 40px;
			text-align: right;
		}
	}
}
