@use '../../setting' as s;
.bl_points {
	&_contents {
		margin-top: 16px;

		@include s.mq() {
			margin-top: 80px;
		}

		@include s.mq(lg) {
			display: flex;
		}
	}

	&_list {
		@include s.mq(lg) {
			margin-right: 24px;
		}
	}

	&_txt {
		&:not(:first-child) {
			margin-top: 40px;

			@include s.mq(sm) {
				margin-top: 64px;
			}

			@include s.mq() {
				margin-top: 80px;
			}
		}
	}

	&_heading {
		color: s.$accentColor02;
		font-size: 15px;
		font-weight: bold;
		display: flex;
		align-items: center;

		@include s.mq(sm) {
			font-size: 16px;
		}

		@include s.mq() {
			font-size: 19px;
		}
	}

	&_heading-num {
		font-size: 24px;
		font-weight: bold;
		width: 42px;
		height: 42px;
		margin-right: 10px;
		display: inline-flex;
		flex-shrink: 0;
		justify-content: center;
		align-items: center;
		background-size: contain;

		@include s.mq() {
			font-size: 32px;
			width: 60px;
			height: 60px;
			margin-right: 24px;
		}
	}

	&_desc {
		margin-top: 8px;

		@include s.mq() {
			margin-top: 24px;
		}
	}

	&_imgs {
		flex-shrink: 0;
		margin-top: 48px;

		@include s.mq(lg) {
			margin-top: 0;
		}
	}

	&_img {
		&_wrapper {
			width: 80%;
			margin: 0 auto;

			@include s.mq(lg) {
				width: 325px;
			}

			&:nth-of-type(2n) {
				transform: rotate(6deg);
			}

			&:nth-of-type(2n + 1) {
				transform: rotate(-6deg);
			}
		}
	}
}
