@use '../../setting' as s;
.top_news {
	padding-top: 80px;
	padding-bottom: 20px;
	overflow: hidden;

	&_inner {
		width: 100%;
		background: s.$baseColor02;
		border-radius: 40px;
		position: relative;

		@include s.mq() {
			width: 96%;
			margin: 0 auto;
		}

		&::before {
			content: '';
			width: 180px;
			height: 64px;
			position: absolute;
			top: 0;
			left: 0;
			background: url(s.$imgDir + 'deco_tape.png') center/ contain;
			transform: translateX(-40px) rotate(-24deg);
			opacity: 0.3;
		}

		&::after {
			content: '';
			width: 180px;
			height: 64px;
			position: absolute;
			bottom: 0;
			right: 0;
			background: url(s.$imgDir + 'deco_tape.png') center/ contain;
			transform: translateX(40px) rotate(-24deg);
			opacity: 0.3;
		}
	}

	&_container {
		padding: 40px 0 80px;

		@include s.mq() {
			padding: 56px;
			display: flex;
			justify-content: space-between;
		}
	}

	&_col {
		flex-shrink: 0;
		display: flex;
		flex-direction: column;
		align-items: center;
	}

	&_btns {
		margin-top: 24px;
		display: flex;
		flex-direction: column;
		align-items: center;

		@include s.mq() {
			margin-top: 56px;
		}
	}

	&_btn {
		font-size: 14px;
		font-weight: bold;
		display: flex;
		align-items: center;
		justify-content: center;
		height: 42px;
		width: 160px;
		border-radius: 21px;
		background: s.$baseColor;
		box-shadow: 3px 3px 3px rgba($color: #000000, $alpha: 0.16);

		&:not(:first-child) {
			margin-top: 16px;

			@include s.mq() {
				margin-top: 24px;
			}
		}
	}

	&_contents {
		max-width: 620px;
		width: 100%;
		height: 260px;
		overflow-y: scroll;
		margin-top: 24px;

		@include s.mq() {
			margin-top: 0;
			padding-left: 20px;
			margin-top: 48px;
		}
	}

	&_item {
		width: 100%;
		position: relative;
		display: none;

		&:not(:first-child) {
			margin-top: 40px;

			&::before {
				content: '';
				width: 100%;
				height: 18px;
				position: absolute;
				left: 0;
				right: 0;
				top: -10px;
				transform: translateY(-100%);
				background: url(s.$imgDir + 'deco_dot.png') center / contain
					repeat;
			}
		}
	}

	&_link {
		font-size: 12px;
		font-weight: bold;
		width: 100%;
		height: 48px;
		border-radius: 24px;
		display: flex;
		align-items: center;
		padding: 0 24px;
		position: relative;
		white-space: nowrap;
		overflow: hidden;
		background: s.$baseColor;

		@include s.mq() {
			font-size: 14px;
			letter-spacing: 0.05em;
		}

		&::before {
			width: 40px;
			height: 1px;
			background: #000;
			border-radius: 0.5px;
			position: absolute;
			left: 132px;
			top: 50%;
			transform: translateY(-50%);

			@include s.mq(lg) {
				content: '';
			}
		}

		&::after {
			content: '';
			width: 24px;
			height: 24px;
			position: absolute;
			right: 24px;
			top: 50%;
			transform: translateY(-50%);
			background: url(s.$imgDir + 'deco_news-arrow.png') center / contain
				no-repeat;
		}
	}

	&_date {
		color: s.$accentColor02;
		font-size: 12px;
		font-weight: bold;
		margin-right: 24px;

		@include s.mq() {
			font-size: 14px;
		}

		@include s.mq(lg) {
			margin-right: 100px;
		}
	}
}
