@use '../../setting' as s;
.el_title-lv2 {
	font-size: 18px;
	font-weight: bold;
	text-align: center;
	white-space: nowrap;
	letter-spacing: 0.05em;
	line-height: 1.8em;
	position: relative;
	display: inline-block;
	padding: 0 40px;

	@include s.mq(sm) {
		font-size: 22px;
		padding: 0 24px;
	}

	@include s.mq() {
		font-size: 27px;
		padding: 0 80px;
	}

	&::after {
		content: '';
		width: 100%;
		height: 18px;
		position: absolute;
		left: 50%;
		top: 1.8em;
		transform: translate(-50%, -25%);
		background: url(s.$imgDir + 'deco_dot.png') center / contain repeat;
	}

	&__break-sm {
		&::after {
			top: 1.8em * 2;
			@include s.mq(sm) {
				top: 1.8em;
			}
		}
	}

	&_sub {
		font-size: 14px;
		line-height: 1.8em;
		text-align: center;
		letter-spacing: 0.05em;
		padding-top: 18px;
		display: block;
	}

	&_wrapper {
		text-align: center;
		overflow: hidden;

		&__bg-white {
			width: 92%;
			max-width: 980px;
			margin: 0 auto;
			height: 200px;
			padding-top: 16px;
			background: url(s.$imgDir + 'bg_section-title_sm.png') center top /
				contain no-repeat;

			@include s.mq(sm) {
				height: 180px;
				background: url(s.$imgDir + 'bg_section-title.png') center top /
					contain no-repeat;
			}
		}
	}
}
