@use '../../setting' as s;
@use '../../mixin' as m;
.bl_join {
	&_inner {
		background: s.$subColor02;
		border-radius: 40px;
		padding: 32px 4%;
		margin-top: 32px;
		width: 100%;
		max-width: s.$contentWidth + 80px * 2;
		overflow: hidden;

		@include s.mq() {
			padding: 80px;
			margin: 24px auto 0;
		}
	}

	&_contents {
		padding: 40px 40px 96px;
		border-radius: 40px;
		background: s.$baseColor;
		position: relative;
		@include m.zIndex();
		max-width: s.$contentWidth;

		@include s.mq() {
			padding-top: 96px;
		}

		&::before {
			background: url(s.$imgDir + 'deco_paper.png') right top / contain
				no-repeat;
			width: 160px;
			height: 160px;
			position: absolute;
			top: -12px;
			right: -32px;
			z-index: -1;

			@include s.mq() {
				content: '';
				top: -32px;
				right: -56px;
			}
		}

		&::after {
			content: '';
			background: url(s.$imgDir + 'deco_paper.png') left bottom / contain
				no-repeat;
			width: 132px;
			height: 132px;
			position: absolute;
			bottom: -12px;
			left: -32px;
			z-index: -1;

			@include s.mq() {
				width: 160px;
				height: 160px;
				bottom: -32px;
				left: -56px;
			}
		}
	}

	&_btn {
		&_wrapper {
			text-align: right;
			margin-top: 56px;
		}
	}
}

.bl_join-table {
	width: 100%;

	&:not(:first-of-type) {
		margin-top: 56px;

		@include s.mq() {
			margin-top: 80px;
		}
	}

	&_title {
		font-weight: bold;
		text-align: center;
		letter-spacing: 0.05em;
		width: 80%;
		padding: 16px 0;
		background: #fac29b;
		position: relative;
		margin: 0 auto 24px;

		@include s.mq() {
			width: 24%;
			padding: 20px 0;
			margin-right: 0;
			margin-left: 0;
		}

		&::before {
			content: '';
			width: calc(100% - 4px);
			height: calc(100% - 4px);
			border: 2px solid s.$accentColor02;
			position: absolute;
			top: 2px;
			left: 2px;
			bottom: 2px;
			right: 2px;
		}
	}

	&_row {
		display: flex;
		flex-direction: column;

		@include s.mq() {
			flex-direction: row;
		}

		&:not(:first-of-type) {
			margin-top: 10px;
		}
	}

	&_head {
		font-weight: bold;
		display: flex;
		align-items: center;
		justify-content: center;
		letter-spacing: 0.05em;
		width: 100%;
		padding: 16px 0;
		flex-shrink: 0;
		background: #fac29b;

		@include s.mq() {
			width: 24%;
			padding: 20px 0;
			margin-right: 10px;
		}
	}

	&_data {
		font-weight: bold;
		display: flex;
		align-items: center;
		letter-spacing: 0.05em;
		width: 100%;
		padding: 16px;
		background: s.$subColor02;

		@include s.mq() {
			padding: 20px 32px;
		}
	}
}
