@use '../../setting' as s;
@use '../../mixin' as m;
.page-access_unit {
	position: relative;
	padding-bottom: 80px;

	@include s.mq() {
		padding-bottom: 120px;
	}

	&:not(:first-of-type) {
		margin-top: 40px;

		@include s.mq() {
			margin-top: 120px;
		}
	}

	&::after {
		content: '';
		width: 100%;
		height: 10px;
		background-image: repeating-linear-gradient(
			-45deg,
			transparent,
			transparent 5px,
			s.$accentColor 5px,
			s.$accentColor 6px
		);
		position: absolute;
		bottom: 0;
		left: 50%;
		transform: translateX(-50%);

		@include s.mq() {
			width: 80%;
		}
	}

	&_info {
		@include s.mq() {
			display: flex;
			justify-content: space-between;
		}
	}

	&_list {
		margin-top: 16px;

		@include s.mq() {
			margin-top: 32px;
		}

		&_wrapper {
			@include s.mq() {
				width: 45%;
			}
		}
	}

	&_name {
		color: s.$accentColor02;
		font-size: 20px;
		font-weight: bold;
		margin-top: 16px;

		@include s.mq() {
			margin-top: 40px;
		}
	}

	&_term {
		color: s.$accentColor02;
		letter-spacing: 0.05em;
		padding-top: 8px;

		@include s.mq() {
			padding-top: 24px;
		}
	}

	&_desc {
		letter-spacing: 0.05em;
		padding-bottom: 8px;
		border-bottom: 1px solid s.$accentColor02;

		@include s.mq() {
			margin-top: 8px;
			padding-bottom: 24px;
		}
	}

	&_maps {
		margin-top: 24px;
		width: 100%;

		@include s.mq() {
			width: 50%;
			margin-top: 0;
		}
	}

	&_map {
		width: 100%;
		height: 100%;
		object-fit: cover;
		object-position: left;

		&_wrapper {
			height: 240px;
			width: 100%;
			text-align: right;

			@include s.mq() {
				height: 480px;
			}
		}
	}

	&_map-txt {
		letter-spacing: 0.05em;
		margin-top: 24px;
	}
}
