@use '../../setting' as s;
@use '../../mixin' as m;
.bl_dojo-list {
	&_heading {
		font-size: 18px;
		font-weight: bold;
	}

	&_items {
		width: 100%;
		margin-top: 24px;

		@include s.mq() {
			display: flex;
			flex-direction: column;
			flex-wrap: wrap;
			justify-content: space-around;
			height: calc(72px * 5);
			margin-top: 40px;
		}
	}

	&_item {
		height: 72px;
		width: 100%;
		line-height: 70px;
		border-bottom: 1px solid #ccc;
		letter-spacing: 0.05em;

		@include s.mq() {
			width: 48%;
		}

		&:first-child {
			border-top: 1px solid #ccc;
		}

		&:nth-child(4) {
			margin-bottom: 0;
			@include s.mq() {
				margin-bottom: 72px;
			}
		}

		&:nth-child(5) {
			@include s.mq() {
				border-top: 1px solid #ccc;
			}
		}

		&_inner {
			display: block;

			&:hover {
				color: s.$accentColor02;
			}
		}
	}

	&_num {
		margin-right: 40px;
		letter-spacing: 0.1em;
	}
}
