@use '../../setting' as s;
@use '../../mixin' as m;
.page-question_unit {
	padding: 24px;
	background: s.$baseColor;
	border-radius: 12px;

	@include s.mq() {
		padding: 40px;
	}

	&:not(:first-child) {
		margin-top: 40px;
	}

	&_item {
		position: relative;

		&:not(:first-of-type) {
			padding-top: 40px;
		}

		@include s.mq() {
			display: flex;
			align-items: center;
		}
	}

	&_num {
		color: s.$baseColor;
		font-size: 14px;
		font-weight: bold;
		display: inline-flex;
		justify-content: center;
		align-items: center;
		width: 64px;
		height: 48px;
		flex-shrink: 0;
		border-radius: 12px;

		@include s.mq() {
			font-size: 16px;
			width: 100px;
			height: 56px;
			margin-right: 32px;
		}

		&__q {
			background: s.$accentColor02;
		}

		&__a {
			background: s.$accentColor;
		}
	}

	&_question {
		font-weight: bold;
		letter-spacing: 0.05em;
		margin-top: 16px;

		@include s.mq() {
			margin-top: 0;
		}
	}

	&_answer {
		margin-top: 16px;
		letter-spacing: 0.1em;

		@include s.mq() {
			margin-top: 0;
		}
	}

	&_cross {
		width: 32px;
		height: 32px;
		position: absolute;
		top: calc((48px - 32px) / 2);
		right: 0;

		@include s.mq() {
			top: calc((56px - 32px) / 2);
		}

		&::before {
			content: '';
			width: 100%;
			height: 2px;
			background: s.$accentColor02;
			position: absolute;
			top: 50%;
			left: 0;
			right: 0;
			transform: translateY(-50%);
		}

		&::after {
			content: '';
			width: 2px;
			height: 100%;
			background: s.$accentColor02;
			position: absolute;
			top: 0;
			left: 50%;
			bottom: 0;
			transform: translateX(-50%);
		}
	}
}
