@use '../../setting' as s;
@use '../../mixin' as m;
.page-introduction_tatsuya-history {
	padding: 24px 32px 40px;
	margin-top: 40px;
	border-radius: 40px;
	background: s.$subColor02;

	@include s.mq() {
		padding: 80px 120px;
	}

	&_title {
		font-size: 20px;
		font-weight: bold;
		text-align: center;
	}

	&_row {
		margin-top: 40px;

		@include s.mq() {
			margin-top: 80px;
			display: flex;
			justify-content: space-between;
		}

		&:nth-of-type(2) {
			@include s.mq() {
				flex-direction: row-reverse;
			}
		}
	}

	&_txt {
		&:not(:first-child) {
			margin-top: 40px;
		}

		@include s.mq() {
			width: 45%;
			margin-top: 0;
		}
	}

	&_img {
		&_wrapper {
			&:not(:first-child) {
				margin-top: 40px;

				@include s.mq() {
					margin-top: 0;
				}
			}

			@include s.mq() {
				width: 45%;
			}
		}
	}
}
