@use '../../setting' as s;
@use '../../mixin' as m;
.single_main {
	&_title {
		font-size: 18px;
		font-weight: bold;

		@include s.mq() {
			font-size: 20px;
		}
	}

	&_date {
		font-size: 12px;
		letter-spacing: 0.1em;
		margin-top: 8px;
	}

	&_content {
		padding-bottom: 80px;
		border-bottom: 1px solid #000;

		div,
		dl,
		ul,
		ol,
		h1,
		h2,
		h3,
		h4,
		h5,
		h6,
		pre,
		code,
		form,
		fieldset,
		legend,
		input,
		textarea,
		p,
		blockquote {
			margin-top: 56px;
		}
	}

	&_btn {
		&_wrapper {
			margin-top: 16px;
			text-align: right;
		}
	}
}
