@use '../../setting' as s;
@use '../../mixin' as m;
.top_contact {
	&_inner {
		background: s.$subColor02;
		border-radius: 40px;
		padding: 32px 4%;
		width: 100%;
		max-width: s.$contentWidth + 80px * 2;
		overflow: hidden;

		@include s.mq() {
			padding: 96px 80px;
			margin: 24px auto 0;
		}
	}

	&_contents {
		padding: 32px 16px;
		border-radius: 40px;
		background: s.$baseColor;
		position: relative;
		max-width: s.$contentWidth;

		@include s.mq() {
			padding: 96px 40px;
		}
	}

	&_desc {
		margin-top: 24px;
		text-align: center;

		@include s.mq() {
			margin-top: 56px;
		}
	}

	&_btn {
		&_wrapper {
			text-align: center;
			margin-top: 40px;

			@include s.mq() {
				margin-top: 64px;
			}
		}
	}
}

.top_contact-form {
	max-width: 690px;
	width: 100%;
	margin: 16px auto 0;

	@include s.mq() {
		margin-top: 80px;
	}

	&_row {
		display: flex;
		flex-direction: column;

		@include s.mq() {
			flex-direction: row;
		}
	}

	&_head {
		margin-top: 24px;

		@include s.mq() {
			width: 40%;
			padding: 20px 0;
			margin: 0;
		}

		&__any {
			&::after {
				content: '任意';
				color: s.$accentColor02;
				font-size: 14px;
				font-weight: bold;
				margin-left: 16px;
			}
		}
	}

	&_data {
		margin-top: 8px;

		@include s.mq() {
			width: 60%;
			padding: 20px 0;
			margin: 0;
		}
	}

	&_input {
		font-size: 16px;
		width: 100%;
		padding: 8px;
		border: 1px solid #ccc;
		border-radius: 8px;
		background: s.$subColor02;
	}

	&_textarea {
		font-size: 16px;
		width: 100%;
		padding: 8px;
		border: 1px solid #ccc;
		border-radius: 8px;
		background: s.$subColor02;
	}
}
