@use '../../setting' as s;
@use '../../mixin' as m;
.page-staff_unit {
	padding-bottom: 40px;
	position: relative;

	@include s.mq() {
		padding-right: 40px;
		padding-left: 40px;
		display: flex;
		align-items: center;
		padding-bottom: 80px;
	}

	&:not(:first-of-type) {
		margin-top: 40px;

		@include s.mq() {
			margin-top: 80px;
		}
	}

	&::after {
		content: '';
		width: 100%;
		height: 10px;
		background-image: repeating-linear-gradient(
			-45deg,
			transparent,
			transparent 5px,
			s.$accentColor 5px,
			s.$accentColor 6px
		);
		position: absolute;
		bottom: 0;
		left: 50%;
		transform: translateX(-50%);

		@include s.mq() {
			width: 80%;
		}
	}

	&_img {
		&_wrapper {
			position: relative;
			padding: 0 16px;

			@include s.mq() {
				width: 420px;
				flex-shrink: 0;
				margin-right: 80px;
				padding: 0;
			}
		}
	}

	&_name {
		color: s.$baseColor;
		font-size: 16px;
		font-weight: bold;
		@include m.marker(s.$accentColor, 1);

		@include s.mq() {
			font-size: 20px;
		}

		&_wrapper {
			position: absolute;
			left: -8px;
			bottom: 24px;

			@include s.mq() {
				left: -16px;
			}
		}
	}

	&_txt {
		margin-top: 24px;

		@include s.mq() {
			margin-top: 0;
		}
	}

	&_desc {
		font-weight: bold;

		&:not(:first-child) {
			margin-top: 16px;
		}
	}

	&_detail {
		font-weight: normal;
	}
}
