@use '../../setting' as s;
@use '../../mixin' as m;
.page-staff_list {
	&_contents {
		margin-top: 40px;
	}

	&_bottom {
		max-width: 720px;
		width: 90%;
		padding: 32px 0;
		margin: 40px auto 0;
		position: relative;
		border-radius: 8px;
		background: s.$baseColor02;

		@include s.mq() {
			margin-top: 80px;
			padding: 40px 0;
		}

		&::before {
			content: '';
			width: calc(100% - 8px * 2);
			height: calc(100% - 8px * 2);
			position: absolute;
			top: 8px;
			right: 8px;
			left: 8px;
			bottom: 8px;
			border: 2px solid s.$accentColor02;
			border-radius: 8px;
		}
	}

	&_bottom-txt {
		text-align: center;
	}
}
