@use '../../setting' as s;
@use '../../mixin' as m;
.bl_info {
	&_desc {
		margin-top: 8px;
	}

	&_contents {
		margin-top: 16px;
		padding: 24px;
		background: s.$baseColor02;
		border: 2px solid s.$accentColor02;
		border-radius: 40px;

		@include s.mq() {
			padding-right: 40px;
			padding-left: 40px;
		}
	}

	&_list {
		margin-top: 8px;

		@include s.mq() {
			display: flex;
		}
	}

	&_term {
		font-size: 16px;
		font-weight: bold;
		border-bottom: 1px dashed #000;
		padding-bottom: 3px;

		@include s.mq() {
			width: 50%;
			padding: 16px 16px 16px 56px;
			border-bottom: none;
			border-right: 1px dashed #000;
		}
	}

	&_definition {
		padding-top: 4px;

		@include s.mq() {
			width: 50%;
			padding: 16px 0 16px 56px;
		}
	}
}
