@use '../../setting' as s;
@use '../../mixin' as m;
.top_voice {
	&_contents {
		margin-top: 40px;

		@include s.mq() {
			margin-top: 80px;
		}
	}
}

.top_voice-head {
	width: 96%;
	margin: 16px auto 0;

	@include s.mq(sm) {
		margin-top: 80px;
	}

	@include s.mq() {
		width: 720px;
	}

	&_copy {
		font-size: 16px;
		font-weight: bold;
		letter-spacing: 0.05em;
		position: relative;
		padding-left: 32px;
		border-bottom: 1px dotted #000;

		@include s.mq(sm) {
			font-size: 20px;
		}

		&::before {
			content: '';
			width: 20px;
			height: 20px;
			position: absolute;
			top: 0.5em * 1.8;
			left: 4px;
			transform: translateY(-50%);
			background: #000;
			border-radius: 1px;
		}
	}

	&_contents {
		display: flex;
		margin-top: 16px;

		@include s.mq() {
			margin-top: 24px;
		}
	}

	&_icon {
		&_wrapper {
			width: 120px;
			flex-shrink: 0;
			margin-left: 16px;

			@include s.mq(sm) {
				width: 144px;
			}

			@include s.mq() {
				margin-left: 0;
			}
		}
	}

	&_txt {
		margin-left: 10px;
		margin-top: 16px;

		@include s.mq() {
			margin-left: 80px;
		}
	}
}

.top_voice-unit {
	padding-top: 120px;
	position: relative;

	@include s.mq() {
		padding-top: 180px;
	}

	&:not(:first-of-type) {
		margin-top: 40px;

		@include s.mq() {
			margin-top: 80px;
		}
	}

	&_num {
		font-size: 28px;
		line-height: 1.2;
		font-weight: bold;
		width: 76px;
		height: 64px;
		border-radius: 15px;
		background: s.$baseColor;
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		margin-bottom: 40px;
		position: absolute;
		left: 15%;
		top: 24px;

		@include s.mq() {
			font-size: 40px;
			width: 120px;
			height: 100px;
			left: 16px;
		}

		&::after {
			content: '';
			width: 8px;
			height: 40px;
			background: s.$baseColor;
			position: absolute;
			bottom: 0;
			left: 50%;
			transform: translate(-50%, 100%);
		}
	}

	&_num-mean {
		font-size: 10px;
		position: relative;

		@include s.mq() {
			font-size: 14px;
		}

		&::before {
			content: '';
			width: 2px;
			height: 14px;
			background: #000;
			transform: translate(-100%, -50%) rotate(-20deg);
			position: absolute;
			top: 50%;
			left: -10px;
		}

		&::after {
			content: '';
			width: 2px;
			height: 14px;
			background: #000;
			transform: translate(100%, -50%) rotate(20deg);
			position: absolute;
			top: 50%;
			right: -10px;
		}
	}

	&_person-img {
		&_wrapper {
			width: 40%;
			position: absolute;
			top: 0;
			left: calc(15% + 76px + 20px);
			@include m.zIndex();

			@include s.mq(sm) {
				width: 160px;
			}

			@include s.mq() {
				width: 200px;
				left: 16px + 120px + 20px;
			}
		}
	}

	&_balloon {
		width: 450px;
		height: 120px;
		background: url(s.$imgDir + 'deco_balloon.svg') center / auto no-repeat;
		display: none;

		@include s.mq(lg) {
			display: flex;
			position: absolute;
			top: 24px;
			left: 16px + 120px + 20px + 200px + 16px;
			@include m.zIndex();
		}
	}

	&_balloon-txt {
		font-size: 14px;
		margin: auto;
	}

	&_inner {
		position: relative;
		padding: 80px 32px 40px;
		background: s.$baseColor;
		border-radius: 40px;

		@include s.mq() {
			padding: 80px 80px 40px;
		}
	}

	&_heading {
		font-size: 14px;
		position: absolute;
		top: 16px;
		left: 32px;

		@include s.mq() {
			top: 24px;
			left: 16px + 120px + 20px + 200px + 16px;
		}
	}

	&_txt {
		line-height: 2;

		&:not(:first-of-type) {
			margin-top: 40px;
		}
	}

	&_question {
		color: s.$accentColor02;
	}

	&_answer {
		color: s.$accentColor;
	}
}
