@use '../../setting' as s;
@use '../../mixin' as m;
.page-access_table {
	background: #fac29b;
	width: 100%;
	margin-top: 40px;

	&_title {
		font-weight: bold;
		letter-spacing: 0.05em;
		padding-bottom: 16px;
	}

	&_body {
		width: 100%;
		display: block;
		padding: 16px;

		@include s.mq() {
			padding: 32px;
		}
	}

	&_row {
		display: flex;
		flex-direction: column;
		gap: 10px;

		&:not(:first-of-type) {
			margin-top: 10px;
		}

		@include s.mq() {
			flex-direction: row;
		}
	}

	&_head {
		font-weight: bold;
		letter-spacing: 0.05em;
		border-radius: 12px;
		background: s.$baseColor;
		flex-shrink: 0;
		display: flex;
		align-items: center;
		justify-content: center;
		padding: 16px 0;

		@include s.mq() {
			width: 24%;
		}
	}

	&_data {
		letter-spacing: 0.05em;
		border-radius: 12px;
		background: s.$baseColor;
		width: 100%;
		padding: 16px 8px;
		display: flex;
		align-items: center;
		justify-content: center;

		@include s.mq() {
			padding-left: 32px;
			justify-content: start;
		}
	}
}
