@use '../../setting' as s;
.el_heading-deco {
	font-size: 16px;
	font-weight: bold;
	line-height: 20px * 1.8;
	display: inline-block;
	padding: 0 32px;
	position: relative;

	@include s.mq() {
		font-size: 20px;
		padding: 0 40px;
	}

	&::before {
		content: '';
		width: 20px;
		height: 20px;
		background: url(s.$imgDir + 'deco_heading-attention.svg') center /
			contain;
		position: absolute;
		top: 50%;
		left: 0;
		transform: translateY(-50%);
	}

	&::after {
		content: '';
		width: 20px;
		height: 20px;
		background: url(s.$imgDir + 'deco_heading-attention.svg') center /
			contain;
		position: absolute;
		top: 50%;
		right: 0;
		transform: scale(-1, 1) translateY(-50%);
	}
}
