@use '../../setting' as s;
@use '../../mixin' as m;
.page-introduction_massage {
	&_contents {
		margin-top: 32px;

		@include s.mq() {
			display: flex;
			flex-direction: row-reverse;
			justify-content: center;
			align-items: center;
		}
	}

	&_img {
		height: 100%;
		object-fit: cover;

		&_wrapper {
			@include s.mq() {
				width: 45%;
				height: 480px;
			}
		}
	}

	&_txt {
		font-weight: bold;
		line-height: 2;
		display: inline;
		@include m.marker(s.$subColor02, 1);

		&_wrapper {
			text-align: center;
			transform: translate(0, 16px);

			@include s.mq() {
				width: 45%;
				text-align: left;
				transform: translate(-24px, 0);
			}
		}
	}
}
