@use '../../setting' as s;
@use '../../mixin' as m;
.bl_karate {
	text-align: center;

	&_inner {
		width: 100%;
		padding: 56px 0 300px;
		border-radius: 40px;
		background: s.$subColor02;
		position: relative;
		margin: 32px auto 32px;

		@include s.mq() {
			width: 92%;
			padding: 72px 0 144px;
			margin-bottom: 72px;
		}
	}

	&_catch-copy {
		font-size: 18px;
		font-weight: bold;
		padding: 4px 32px;
		display: inline-block;
		background: s.$baseColor;

		@include s.mq() {
			font-size: 20px;
		}
	}

	&_txt {
		font-size: 13px;
		margin-top: 40px;
		padding: 0 16px;
		line-height: 2.4;
		position: relative;
		@include m.zIndex();

		@include s.mq() {
			font-size: 15px;
			margin-top: 64px;
			padding: 0;
		}

		@include s.mq(sm) {
			padding: 0 40px;
		}
	}

	&_img {
		&_wrapper {
			position: absolute;

			&:nth-of-type(1) {
				width: 42%;
				right: 5%;
				bottom: 128px;

				@include s.mq() {
					width: 175px;
					right: -16px;
					bottom: 375px;
				}
			}

			&:nth-of-type(2) {
				width: 28%;
				left: 3%;
				bottom: 64px;

				@include s.mq() {
					width: 180px;
					left: -40px;
					bottom: 136px;
				}
			}

			&:nth-of-type(3) {
				width: 50%;
				right: 3%;
				bottom: -32px;

				@include s.mq() {
					width: 285px;
					right: -40px;
					bottom: -72px;
				}
			}
		}
	}
}
