@use '../../setting' as s;
@use '../../mixin' as m;
.bl_cta {
	padding: 40px 0;

	&_inner {
		width: 100%;
		max-width: 960px;
		border: 5px solid #f8a468;
		margin: 0 auto;
		padding: 120px 24px 24px;
		position: relative;

		@include s.mq() {
			padding: 24px;
		}

		&::before {
			content: '';
			width: 100px;
			height: 100px;
			position: absolute;
			top: 16px;
			left: 24px;
			background: url(s.$imgDir + 'icon_question-female.png') center /
				contain no-repeat;

			@include s.mq() {
				width: 160px;
				height: 160px;
				left: 60px;
			}
		}

		&::after {
			content: '';
			width: 100px;
			height: 100px;
			position: absolute;
			top: 16px;
			right: 24px;
			background: url(s.$imgDir + 'icon_question-male.png') center /
				contain no-repeat;

			@include s.mq() {
				width: 160px;
				height: 160px;
				right: 60px;
			}
		}
	}

	&_txt {
		font-size: 16px;
		text-align: center;
		letter-spacing: 0.1em;

		@include s.mq() {
			font-size: 24px;
			margin-top: 32px;
		}
	}

	&_txt-en {
		font-size: 12px;
		text-align: center;
		letter-spacing: 0.1em;
		margin-top: 8px;

		@include s.mq() {
			font-size: 14px;
		}
	}

	&_btns {
		margin-top: 24px;
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;

		@include s.mq() {
			margin-top: 40px;
			flex-direction: row;
		}
	}

	&_btn {
		color: s.$baseColor;
		font-size: 12px;
		font-weight: bold;
		text-align: center;
		white-space: nowrap;
		max-width: 320px;
		width: 98%;
		line-height: 60px;
		border-radius: 30px;
		position: relative;
		background: #f8a468;
		padding-left: 24px;
		box-shadow: 6px 6px 6px rgba($color: #000000, $alpha: 0.16);

		@include s.mq() {
			font-size: 16px;
		}

		&:not(:first-of-type) {
			margin-top: 16px;

			@include s.mq() {
				margin-top: 0;
				margin-left: 40px;
			}
		}

		&::before {
			content: '';
			width: 20px;
			height: 20px;
			border-radius: 50%;
			border: 1px solid s.$baseColor;
			position: absolute;
			top: 50%;
			left: 20px;
			transform: translateY(-50%);
		}

		&::after {
			content: '';
			height: 3px;
			width: 16px;
			background: url(s.$imgDir + 'deco_arrow_white.svg') center / contain
				no-repeat;
			position: absolute;
			top: 50%;
			left: 32px;
			transform: translateY(-50%);
			transition: transform 0.12s ease-out;
		}

		&:hover::after {
			transform: translate(4px, -50%);
		}
	}
}
