@use '../../setting' as s;
.el_btn {
	font-weight: bold;
	line-height: 56px;
	white-space: nowrap;
	border-radius: 28px;
	display: inline-block;
	background: #fac29b;
	padding: 0 56px;
	position: relative;
	box-shadow: 6px 6px 6px rgba($color: #000000, $alpha: 0.16);

	@include s.mq(sm) {
		line-height: 60px;
		border-radius: 30px;
		padding: 0 72px;
	}

	&::before {
		content: '';
		width: 20px;
		height: 20px;
		border-radius: 50%;
		border: 1px solid #000;
		position: absolute;
		top: 50%;
		right: 20px;
		transform: translateY(-50%);
	}

	&::after {
		content: '';
		height: 3px;
		width: 24px;
		background: url(s.$imgDir + 'deco_arrow_black.svg') center / contain
			no-repeat;
		position: absolute;
		top: 50%;
		right: 9px;
		transform: translateY(-50%);
		transition: transform 0.12s ease-out;
	}

	&:hover::after {
		transform: translate(4px, -50%);
	}
}
