@use '../../setting' as s;
@use '../../mixin' as m;
.page-introduction_tatsuya {
	&_contents {
		margin-top: 40px;

		@include s.mq() {
			display: flex;
			justify-content: space-between;
		}
	}

	&_img {
		height: 100%;
		object-fit: cover;

		&_wrapper {
			height: 320px;
			overflow: hidden;
			border-radius: 24px;

			@include s.mq() {
				height: 480px;
				width: 45%;
			}
		}
	}

	&_txt {
		margin-top: 24px;

		@include s.mq() {
			width: 45%;
			margin-top: 40px;
		}
	}

	&_name {
		color: s.$accentColor02;
		font-size: 18px;
		font-weight: bold;

		@include s.mq() {
			font-size: 20px;
		}
	}

	&_desc {
		margin-top: 20px;

		@include s.mq() {
			margin-top: 32px;
		}
	}
}
