@use '../../setting' as s;
.top_location {
	&_contents {
		margin-top: 32px;
	}

	&_btn {
		&_wrapper {
			margin-top: 32px;
			text-align: right;
		}
	}

	&_map {
		height: 320px;
		margin-top: 48px;

		@include s.mq() {
			height: 400px;
		}
	}

	&_map-data {
		width: 100%;
		height: 100%;
		border: none;
	}
}
