@use '../../setting' as s;
@use '../../mixin' as m;
.page-introduction_junichi {
	&_contents {
		margin-top: 16px;

		@include s.mq() {
			margin-top: 40px;
			display: flex;
			justify-content: space-between;
		}
	}

	&_name {
		font-size: 20px;
		font-weight: bold;
		padding-left: 32px;
		position: relative;
		border-bottom: 1px solid #000;

		@include s.mq() {
			font-size: 24px;
		}

		&::before {
			content: '';
			width: 20px;
			height: 20px;
			position: absolute;
			top: 0.9em;
			left: 4px;
			transform: translateY(-50%);
			background: #000;
			border-radius: 1px;
		}
	}

	&_position {
		margin-right: 24px;

		@include s.mq() {
			margin-right: 72px;
		}
	}

	&_desc {
		margin-top: 40px;
	}

	&_img {
		&_wrapper {
			margin-top: 40px;
			@include s.mq() {
				margin-top: 160px;
				width: 42%;
				flex-shrink: 0;
			}
		}
	}
}
