@use '../../setting' as s;
@use '../../mixin' as m;
.page-photo-gallery_contents {
	&_desc {
		letter-spacing: 0.1em;
		text-align: center;
		margin-top: 40px;
	}

	&_items {
		max-width: 1200px;
		display: flex;
		flex-wrap: wrap;
		gap: 1px;
		margin-top: 40px;
		padding-bottom: 80px;
	}

	&_item {
		border: 24px solid s.$subColor02;
		width: calc((100% - 1px) / 2);
		height: 50vw;
		overflow: hidden;
		cursor: pointer;

		@include s.mq(xl) {
			width: calc((100% - 3px) / 4);
			height: calc(1200px / 4);
		}

		& > img {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}
}
