@use '../../setting' as s;
@use '../../mixin' as m;

.page-about_thought {
	&_contents {
		margin-top: 32px;

		@include s.mq() {
			display: flex;
		}
	}

	&_txt {
		display: flex;
		flex-direction: column;
		align-items: center;

		@include s.mq() {
			width: 50%;
		}
	}

	&_desc {
		margin-top: 24px;

		@include s.mq() {
			margin-right: 16px;
		}
	}

	&_img {
		object-fit: contain;

		&_wrapper {
			margin-top: 16px;

			@include s.mq() {
				width: 45%;
				flex-shrink: 0;
				margin-top: 0;
			}
		}
	}
}
