@use '../../setting' as s;
@use '../../mixin' as m;
.bl_sns {
	@include s.mq() {
		display: flex;
		width: 100%;
	}

	&_item {
		height: 120px;
		display: flex;
		background-size: cover;
		background-position: center;
		padding: 24px 16px;
		position: relative;

		@include s.mq(sm) {
			height: 160px;
		}

		@include s.mq() {
			width: 50%;
			height: 240px;
		}

		&:not(:first-of-type) {
			margin-top: 1px;

			@include s.mq() {
				margin-top: 0;
				margin-left: 1px;
			}
		}

		&::before {
			content: '';
			position: absolute;
			top: 0;
			left: 0;
			right: 0;
			bottom: 0;
			background: rgba($color: #777474, $alpha: 0.6);
		}
	}

	&_btn {
		color: s.$baseColor;
		font-size: 16px;
		text-align: center;
		letter-spacing: 0.1em;
		white-space: nowrap;
		width: 260px;
		line-height: 56px;
		margin: auto;
		border: 1px solid s.$baseColor;
		border-radius: 28px;
		position: relative;
		@include m.zIndex();

		@include s.mq(sm) {
			width: 280px;
			line-height: 64px;
			border-radius: 32px;
		}

		@include s.mq() {
			font-size: 20px;
			width: 360px;
			line-height: 80px;
			border-radius: 40px;
		}

		&::before {
			content: '';
			width: 32px;
			height: 32px;
			border-radius: 50%;
			border: 1px solid s.$baseColor;
			position: absolute;
			top: 50%;
			right: 24px;
			transform: translateY(-50%);
		}

		&::after {
			content: '';
			height: 4px;
			width: 30px;
			background: url(s.$imgDir + 'deco_arrow_white.svg') center / contain
				no-repeat;
			position: absolute;
			top: 50%;
			right: 9px;
			transform: translateY(-50%);
			transition: transform 0.12s ease-out;
		}

		&:hover::after {
			transform: translate(4px, -50%);
		}
	}
}
