@use '../../setting' as s;
@use '../../mixin' as m;
.el_page-title {
	font-size: 28px;
	line-height: 1.2;
	font-weight: bold;
	white-space: nowrap;
	max-width: 560px;
	width: 100%;
	background: s.$baseColor02;
	position: relative;
	padding-left: 8px;

	@include s.mq() {
		font-size: 32px;
		margin-left: -24px;
		padding-left: 24px;
	}

	&::after {
		content: '';
		width: 100px;
		height: 4px;
		background: s.$accentColor;
		position: absolute;
		top: calc(100% + 24px);
		left: 0;
	}
}
