@use '../setting' as s;
@use '../mixin' as m;
.js_ly_header-hamburger.is_ly_header-hamburger_open {
	background: s.$baseColor02;

	& > .js_ly_header-hamburger_bar {
		background: s.$accentColor;

		&:nth-of-type(1) {
			top: 24px;
			transform: translateX(-50%) rotate(135deg);
		}

		&:nth-of-type(2) {
			top: 24px;
			opacity: 0;
		}

		&:nth-of-type(3) {
			top: 24px;

			transform: translateX(-50%) rotate(-135deg);
		}
	}

	& > .js_ly_header-hamburger_txt {
		color: s.$accentColor;
	}
}

.js_ly_header-hamburger {
	transition: background-color 0.24s;
}

.js_ly_header-hamburger_bar {
	transition: transform 0.24s, opacity 0.24s, background-color 0.24s;
}

.js_ly_header-hamburger_txt {
	transition: color 0.24s;
}
